import React, { Component } from 'react'
import { Row, Col, Button, Menu, Dropdown, Icon, Divider, Badge, Switch, Drawer } from 'antd';
const menu = (
    <Menu>
        <Menu.Item><a target="_blank" rel="noopener noreferrer" href="https://tinypng.com/">TinyPNG</a></Menu.Item>
        <Menu.Item><a target="_blank" rel="noopener noreferrer" href="https://developers.google.com/speed/pagespeed/insights/">PageSpeed Insights</a></Menu.Item>
        <Menu.Item><a target="_blank" rel="noopener noreferrer" href="https://www.caniuse.com/">Can I use...</a></Menu.Item>
        <Menu.Item><a target="_blank" rel="noopener noreferrer" href="https://any86.github.io/any-rule/">正则大全</a></Menu.Item>
        <Menu.Item><a target="_blank" rel="noopener noreferrer" href="http://css2sass.herokuapp.com/">css2sass</a></Menu.Item>
        <Menu.Item><a target="_blank" rel="noopener noreferrer" href="https://www.babeljs.cn/">Babel</a></Menu.Item>
        {/* <Menu.Item><a target="_blank" rel="noopener noreferrer" href="https://marketplace.visualstudio.com/items?itemName=hancel.google-translate">Google Translate</a></Menu.Item> */}
    </Menu>
);
export class Header extends Component {
    state = { visible: false };

    showDrawer = () => {
        this.setState({
            visible: true,
        });
    };

    onClose = () => {
        this.setState({
            visible: false,
        });
    };

    render() {
        return (
            <div className="header">
                <Row>
                    <Col span={24}>
                        <div className="btnList">
                            <Button type="primary"><a target="_blank" rel="noopener noreferrer" href="http://10.126.80.23:61080/share/jds_javascript/demo/">JDS库V1</a></Button>
                            <Button><a target="_blank" rel="noopener noreferrer" href="https://3.swiper.com.cn/">Swiper.js</a></Button>
                            <Button><a target="_blank" rel="noopener noreferrer" href="https://daneden.github.io/animate.css/">Animate.css</a></Button>
                            <Button><a target="_blank" rel="noopener noreferrer" href="https://github.com/Prinzhorn/skrollr/">Skrollr.js</a></Button>
                            <Dropdown overlay={menu}>
                                <Button>
                                    工具 <Icon type="down" />
                                </Button>
                            </Dropdown>
                            <Button onClick={this.showDrawer}>
                                设定 <Icon type="setting" />
                            </Button>
                            <Drawer
                                title="设定"
                                placement="right"
                                closable={false}
                                onClose={this.onClose}
                                visible={this.state.visible}
                            >
                                <p><Switch checkedChildren="暗" unCheckedChildren="明" onChange={this.props.handleDark.bind(this)} /></p>
                            </Drawer>
                            <div className="swList">
                                
                                
                            </div>
                        </div>
                        
                        <h1><img src="https://trvimg.r10s.jp/share/common/images/travel-logo-2017-1line.png" alt="Rakuten Travel"/></h1>
                        <h2><Badge count={'V2.0.0'} style={{ backgroundColor: '#52c41a' }} >乐天旅行常用JS库-JDS</Badge></h2>

                        <Divider />
                    </Col>
                </Row>
            </div>
        )
    }
}

export default Header
