import React, { Component } from 'react'
import MainData from './mainData';
import 'antd/dist/antd.css';
import { Row, Col, Card, Table, Button, Icon, Spin } from 'antd';
import './App.scss';
import Header from './components/Header';
import Footer from './components/Footer';
import NavRight from './components/NavRight';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';

const columns = [
  {
    title: '名称',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '作用',
    dataIndex: 'good',
    key: 'good',
  },
  {
    title: '其他',
    dataIndex: 'more',
    key: 'more',
  },
];

export class App extends Component {
  componentWillMount(){
    const mainData = this.renderMain(MainData);
    this.setState({
      mainData,
    });
  }

  state = {
    loading: true,
    dark : false
  }

  handleDark(val){
    this.setState({
      dark : val
    });
  }

  renderMain = (data) => {
      return data.map((item) => {
          if(item.children){
            return (
              <Card key={item.key} title={item.key + '.' + item.title} id={'Anga'+item.key} headStyle={{fontSize:24}}>
                <div className="ant-about" dangerouslySetInnerHTML = {{__html:item.about}} />
                {item.table ? <Table dataSource={item.table} columns={columns} rowKey="name" pagination={false} bordered /> : ''}
                {item.html ? <SyntaxHighlighter 
                  className="ant-html"
                  showLineNumbers={true}
                  startingLineNumber = {0}
                  language={'html'} 
                >{item.html}</SyntaxHighlighter> : ''}
                {item.link != '##' ? <Button type="primary" block size="large" ><a target="_blank" rel="noopener noreferrer" href={item.link}>查看效果</a></Button> : <Button type="primary" block size="large" disabled>查看效果</Button>}
                {/* 再遍历children */}
                {this.renderMain(item.children)}
              </Card>
            );
          }
          return (
            <Card key={item.key} title={item.key + '.' + item.title} id={'Anga'+item.key} headStyle={{fontSize:24}}>
              <div className="ant-about" dangerouslySetInnerHTML = {{__html:item.about}} />
              {item.table ? <Table dataSource={item.table} columns={columns} rowKey="name" pagination={false} bordered /> : ''}
              {item.html ? <SyntaxHighlighter 
                className="ant-html"
                showLineNumbers={true}
                startingLineNumber = {0}
                language={'html'} 
              >{item.html}</SyntaxHighlighter> : ''}
              {item.link != '##' ? <Button type="primary" block size="large" ><a target="_blank" rel="noopener noreferrer" href={item.link}>查看效果</a></Button> : <Button type="primary" block size="large" disabled>查看效果</Button>}
            </Card>
          )
      })
  }

  render() {
    // 加载页面
    if(this.state.loading){
      const antIcon = <Icon type="loading" style={{ fontSize: 60 }} spin />;
      return <div className="loading"><Spin indicator={antIcon} /></div>;
    }
    return (
      <div className={this.state.dark ? 'wrapper dark' : 'wrapper'}>
      {this.state.dark}
        <Header handleDark={this.handleDark.bind(this)}/>
        <Row>
          <Col span={20}>
            <div className="main">
              {this.state.mainData}
            </div>
          </Col>
          <NavRight/>
        </Row>
        <Footer/>
      </div>
    )
  }

  componentDidMount(){
    // 不放在 setTimeout 貌似不工作
    setTimeout(() => {
      this.setState({
        loading : false
      });
    }, 0);
  }
}

export default App;
