import React, { Component } from 'react'
import MenuConfig from './../mainData';
import { Col, Anchor } from 'antd';
const { Link } = Anchor;

export class NavRight extends Component {
    componentWillMount(){
        const menuMainData = this.renderMenu(MenuConfig);
        this.setState({
            menuMainData
        });
    }

    renderMenu = (data) => {
        return data.map((item) => {
            if(item.children){
              return (
                <Link key={item.key} href={'#Anga'+item.key} title={item.key + '.' + item.title}>
                  {/* 再遍历children */}
                  {this.renderMenu(item.children)}
                </Link>
              );
            }
            return <Link key={item.key} href={'#Anga'+item.key} title={item.key + '.' + item.title} />
        })
    }

    render() {
        return (
            <div className="navRight">
                <Col span={4}>
                    <Anchor>
                        {/*
                         <Link href="#components-anchor-demo-basic" title="Basic demo" />
                        <Link href="#components-anchor-demo-static" title="Static demo" />
                        <Link href="#API" title="API">
                            <Link href="#Anchor-Props" title="Anchor Props" />
                            <Link href="#Link-Props" title="Link Props" />
                        </Link> 
                        */}
                        {this.state.menuMainData}
                    </Anchor>
                </Col>
            </div>
        )
    }
}

export default NavRight
