import React, { Component } from 'react'
import { Row, Col, Divider, BackTop } from 'antd';

export class Footer extends Component {
    render() {
        return (
            <div className="footer">
                <Row>
                    <Col span={24}>
                        <Divider />
                        <h2>Powered by React, Ant Design, Git in 2019</h2>
                        <BackTop />
                    </Col>
                </Row>
            </div>
        )
    }
}

export default Footer
